<template>
  <div class="container">
    <mt-header title="" class="topNav">
      <mt-button icon="back" @click="back" slot="left"></mt-button>
      <mt-button @click="edit" slot="right"></mt-button>
    </mt-header>
    <div class="clear"></div>
    <div class="totalNum">{{totalNames}}</div>
    <div class="list">
      <ul>
        <li v-for="checkName in checkNames">
          <div class="name">
            <i class="icon icon-form-name"></i>
            <span>{{checkName}}</span>
          </div>
        </li>
      </ul>
    </div>
    <footer class="footer" v-show="addFocus">
      <span @click="addPerson"></span>
      <span></span>
    </footer>
  </div>

</template>
<style scoped>
  .container {
    width: 100%;
    height: 100vh;
    background-color: #f4f4f4;
  }

  .head {
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: #ffffff;
  }

  .head > div:first-child {
    width: 100%;
    float: left;
    font-size: 20px;
    color: #a0a0a0;
  }

  .head > div:nth-child(2) {
    width: 20px;
    height: 30px;
    float: left;
    margin-left: -100%;
    background-color: red;
  }

  .head > div:last-child {
    color: #3f56ff;
    font-size: 16px;
    margin-left: -66px;
    float: left;
  }

  .clear {
    clear: both;
  }

  .totalNum {
    height: 30px;
    line-height: 30px;
    font-size: 16px;
    color: #a0a0a0;
  }

  .list {
    width: 100%;
    height: 40em;
  }

  ul > li {
    background-color: #ffffff;
    text-align: left;
    height: 43px;
    line-height: 43px;
    margin-bottom: 2px;
  }

  .list .name > span {
    display: inline-block;
    color: #a0a0a0;
    font-size: 18px;

  }

  .list .name > span:first-child {
    width: 10px;
    background-color: red;
    height: 10px;
  }

  .list .name > span:last-child {
    margin-left: 20px;
  }
  i.icon.icon-form-name {
    margin-left: 15px;
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/tx.svg);
  }
  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }

  .footer {
    margin-top: 2em;
    font-size: 16px;
    color: #a0a0a0;
  }

  .footer > span:first-child {
    display: inline-block;
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/icon.png);
  }

  .footer > span:last-child {
    display: block;
  }
</style>

<script>
  import Vue from 'vue'
  import {Checklist} from 'mint-ui';
  Vue.component(Checklist.name, Checklist);
  export default {
    name: 'Focus',
    data(){
      return {
        checkNames: [],
        totalNames: 0,
        options: [],
        value: [],
        addFocus: false,
      }
    },
    mounted(){
      var that = this;
      if (this.$route.query.checkNames) {
        that.checkNames=[];
        this.$route.query.checkNames.forEach(function (value) {
          that.checkNames.push(value);
        });
        console.log(this.checkNames);
      }

//      this.getPersonOfFocus();
      this.totalNum();
    },
    methods: {
      getPersonOfFocus(){
        $.ajax({
          type: "get",
          url: this.$api + '/notice/find',
          data: {
            orgid: this.$route.params.orgid,
            grade: this.$route.params.riskGrade
          },
          dataType: "json",
          success: function (data) {
            debugger
            if (data.success == true) {
              for (var i = 0; i < data.list.length; i++) {
                this.options[i] = {
                  label: data.list[i].name,
                  value: data.list[i].noticer
                }
              }
              alert(JSON.stringify(this.options));
            }
          }.bind(this)
        });

      },
      totalNum(){
        if (this.checkNames) {
          this.totalNames = this.checkNames.length;
        }
      },
      edit(){
        this.addFocus = true;
      },
      back(){
      },
      addPerson(){
        this.$router.push({
          name: "addpersom",
          query: {orgid: this.$route.params.orgid, grade: this.$route.params.riskGrade, names:this.checkNames}
        })
      }
    }
  }

</script>



