var utils = {
  addCookie : function(objName,objValue,objHours){//cookie
    var str = objName + "=" + encodeURIComponent(objValue);
    if(objHours > 0){    //0cookie
      var date = new Date();
      var ms = objHours*3600*1000;
      date.setTime(date.getTime() + ms);
      str += ";expires=" + date.toGMTString();
    }
    document.cookie = str + ";path=/";
  },

  //CookieCookie
  getCookie : function(cookieName){
    console.log(document.cookie)
    var allCookies = document.cookie;
    var cpos = allCookies.indexOf(cookieName);
    if (cpos != -1){
      cpos += cookieName.length + 1;
      var cend = allCookies.indexOf(";", cpos);
      if (cend == -1){
        cend = allCookies.length;
      }
      var cookieValue = allCookies.substring(cpos, cend);
      return decodeURIComponent(cookieValue);
    }
    return null;
  },
  //CookieCookie
  delCookie : function(name){
    var cookieValue = this.getCookie(name);
    if(cookieValue != null){
      var exp = new Date();
      exp.setTime(exp.getTime() - 1000);
      document.cookie = name + "=" + cookieValue + ";path=/;expires=" + exp.toGMTString();
    }
  },
  formatDate(value){
    var date = new Date(parseInt(value));
    var mon = parseInt(date.getMonth(),10) + 1;
    var day = parseInt(date.getDate(),10);
    var hour = parseInt(date.getHours(),10);
    var min = parseInt(date.getMinutes(),10);
    if(mon < 10)
      mon = "0" + mon;
    if(day < 10)
      day = "0" + day;
    if(hour < 10)
      hour = "0" + hour;
    if(min < 10)
      min = "0" + min;
    return date.getFullYear() + "-" + mon + "-" + day +" " + hour + ":" + min;
  }
}

export default utils



