<style scoped>
  a{
    text-decoration: none;

  }
  .am-navbar {
    font-size:14px;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    z-index: 1010;
    border-top: 1px #b8b8b8 solid;
  }
  .am-navbar-default .am-navbar-nav {
    background-color: #fff;
  }
  .am-navbar .am-navbar-nav {
    padding-left: 8px;
    padding-right: 8px;
    text-align: center;
    overflow: hidden;
  }
  .am-navbar ul {
    padding-left: 0;
    margin: 0;
    list-style: none;
    width: 100%;
  }

  .am-navbar-nav {
    position: relative;
    z-index: 1015;
  }

  .am-navbar .am-navbar-nav li {
    display: table-cell;
    width: 1%;
    float: none;
    cursor: pointer;
  }
  .am-navbar-nav li:not(:last-child){
    border-right: 1px solid #b8b8b8;
  }
  .am-avg-sm-4>li:nth-of-type(4n+1) {
    clear: both;
  }

  .am-avg-sm-4>li:nth-of-type(n) {
    clear: none;
  }
  .am-avg-sm-4>li {
    width: 25%;
  }
  [class*=am-avg-]>li {
    display: block;
    height: auto;
    float: left;
  }
</style>
<template>
  <div data-am-widget="navbar" class="am-navbar am-cf am-navbar-default am-no-layout" id="">
    <ul class="am-navbar-nav am-cf am-avg-sm-4">
      <li @click="pageTo('main')">
        <span class="am-navbar-label"></span>
      </li>
      <li @click="pageTo('report')">
        <span class="am-navbar-label"></span>
      </li>
    </ul>
    <ul class="am-navbar-actions"></ul>
  </div>
</template>

<script>

  export default {
    name: 'FooterNav',
    methods: {
      pageTo(componentName){
        this.$router.push({name: componentName});
      }
    }
  }
</script>

