<style scoped>
  .layout-content {
    margin-top: 10px;
    background-color: #fff;
  }

  .layout-content-main {
    padding: 10px;
    font-size: 14px;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
  }

  .layout-ceiling-main a {
    color: #9ba7b5;
  }

  .item {
    border-bottom: 1px solid #8EBEC4;
    font-size: 15px;
    line-height: 35px;
  }

  .item > div {
    margin: auto 10px;
  }

  .filter > div {
    margin: 5px auto;
    padding: 5px;
    margin-left: 10px;
  }

  .main {
    /* min-height: 300px;*/
    background-color: #FFFFFF;
  }

  .submenu {
    border-bottom: 1px solid #8EBEC4;
  }

  .submenu > div {
    margin: auto 10px;
  }

  .layout-ceiling-main a {
    color: #9ba7b5;
  }

  .filter div {

    margin-left: 10px;
  }

  .item-detail > div {
    text-align: left;
    margin: 10px auto;
  }

  /*.detail div{
    margin: 10px auto;
  }*/
  .title {
    font-size: 15px !important;
    line-height: 35px;
  }

  .demo-carousel {
    text-align: center;
  }

  .info_t {
    border-bottom: 1px solid #3091f2;
    margin-bottom: 10px;
  }

  .info_t > div {
    text-align: center;
    color: #fff;
    width: 60px;
    background-color: #3091f2;
  }
</style>
<template>
  <div class="layout-content" :model="item" @click="showDetail(item)">
    <div class="submenu">
      <Row type="flex" justify="start" class="title">
        <i-col span="19" style="text-align: left">
          <span style="font-weight: 900"><strong>{{item.title}}</strong></span>
        </i-col>
        <Col span="5" style="text-align:right; display: none">
        <a href="javascript:void(0)" @click="goBack"></a>
        </Col>
      </Row>
    </div>
    <div class="layout-content-main">
      <div class="item-detail">
        <div>
          &nbsp;
          <span>{{item.buildingName}}  &nbsp;&nbsp;{{item.floorIndex}}&nbsp;&nbsp;  {{item.roomno}}</span>
        </div>
        <div style="display: none">
          &nbsp;{{item.orgName}}
        </div>
        <div >
          <span :style="riskGradeColor">&nbsp;{{getRiskGrade(item.riskGrade)}}</span>
        </div>
        <Row style="display: none">
          <Col span="12">
          {{item.inspectorname}}</Col>
        </Row>
        <Row>
          <Col span="12">
           {{item.inspecttime|timeFilter}}</Col>
        </Row>
        <Row>
          <Col span="24">
          {{getEventStatus(item.status)}}</Col>
        </Row>
        <div v-if="item.status == 3">
          {{item.limittime}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  /*
    import 'iview/dist/styles/iview.css'
  */

  export default {
    name: 'inspectionItem',
    data() {
      return {

        eventStatus: {
          1: '',
          2: '',
          3: '',
          4: ''
        },
        riskType: {
          3001: '',
          3002: '',
          3003: '',
          3004: '',
          3005: ''
        },
        targetType: {
          1001: '',
          1002: '',
          1003: '',
          1004: '',
          1000: ''
        },
        columns1: [
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'orgName'
          },
          {
            title: '',
            key: 'position'
          },
          {
            title: '',
            key: 'mobile'
          }
        ],
        riskGrade:[
          {
            title:"",
            color:"#00FF00"
          },
          {
            title:"1",
            color:"#8f1010"
          },
          {
            title:"2",
            color:"#EE0000"
          },
          {
            title:"3",
            color:"#EE7600"
          },
          {
            title:"4",
            color:"#EEEE00"
          }
        ],
        riskGradeColor:""
      }
    },
    props: ['item', 'index'],
    methods: {
      showDetail(data) {
        this.$router.push({
          name: 'detail', params: {
            id: data.id,
            riskGrade: data.riskGrade, target: data.target ? data.target : '0'
            , roomId: data.roomid
          }
        });
      },
      getEventStatus(status) {
        return this.eventStatus[status];
      },
      getRiskType(type) {
        return this.riskType[type];
      },
      getTargetType(type) {
        return this.targetType[type];
      },
      getRiskGrade(type){
        this.riskGradeColor="color:"+this.riskGrade[type].color;
        return this.riskGrade[type].title;
      },
      goBack() {
        eventBus.$emit('backToGarden');
        this.$router.push({path: '/main/inspection'})
      }

    }
  }

</script>



