<style scoped>
  html, body, #app{
    height: 100%;
  }
  body{
    display: block;
  }
  .main_content{
    height: 100%;
    display: inline-block;
    width: 100%;
  }
  .main{
    font-size: 14px;
    width: 98%;
    margin: 50px 1% 40px;
    /* height: 100%;
     overflow: hidden;*/
  }
  select {
    width: 100px;
    height: 30px;
    border-radius: 3px;
  }
  i.icon.icon-form-tj {
    width: 39px;
    height: 39px;
    background-image: url(../assets/img/tjtp.svg);
  }
  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }
  .item{
    border-bottom: 1px solid #8EBEC4;
    font-size: 15px;
    line-height: 35px;
    text-align: left;
  }
  .item>div{
    margin: auto 10px;
  }
  .topNav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    z-index: 1010;
    background-color: #26a2ff;
    color: #ffffff;
  }
</style>
<template>
  <div class="main_content" >
    <mt-header title="" class="topNav">
      <router-link :to="{name: 'main'}" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="main" v-infinite-scroll="loadMore"

         infinite-scroll-immediate-check="true"
         infinite-scroll-distance="10">
      <!--<div id="scroller" :style="scrollStyle">-->

      <div class="item" v-for="(item, index) in warningList" :key="index">
        <div>
          <strong>{{item.title}}</strong>
        </div>
        <div>
          {{item.content}}
        </div>
      </div>
    </div>
    <FooterNav></FooterNav>
  </div>
</template>

<script>
  import FooterNav from '../components/footer.vue'
  import utils from '../assets/js/utils.js'
  import Vue from 'vue'
  import { Header } from 'mint-ui';

  Vue.component(Header.name, Header);

  export default {
    name: 'EventList',
    data(){
      return{
        wrapperH: 0,
        scrollStyle: {
          height: this.wrapperH + 'px'
        },
        pageIndex: this.$route.query.pageIndex,
        pageCount: this.$route.query.pageCount,
        totalPage: 0,
        loading: false,
        warningList: []
      }
    },
    components: {
      FooterNav
    },
    created(){
      this.getWarningList(this.$route.query.pageIndex, this.$route.query.pageCount);
    },
    methods:{
      loadMore(){
        if(this.pageIndex < this.totalPage){
          this.getScrollList(this.pageIndex+1, this.pageCount);
        }else{
          this.$Message.success('...');
        }
      },
      getWarningList(eventStatus, eventType, pageIndex, pageCount){
        var cookie = utils.getCookie('token');
        if(cookie == null){
          this.$router.push({path: '/login'})
        }
        $.ajax({
          type : "get",
          url : this.$api+'/warning/find',
          data: {
            userid: cookie.split('|')[0],
            eventStatus: eventStatus,
            eventType: eventType,
            pageIndex: pageIndex,
            pageCount: pageCount
          },
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              this.totalPage = parseInt(data.count/this.pageCount)
                + (data.count%this.pageCount>0? 1: 0);
              this.warningList = data.list;
            }
          }.bind(this)
        });
      },
      getScrollList(eventStatus, eventType, pageIndex, pageCount){
        var cookie = utils.getCookie('token');
        if(cookie == null){
          this.$router.push({path: '/login'})
        }
        this.loading = true;
        $.ajax({
          type : "get",
          url : this.$api+'/warning/find',
          data: {
            pageIndex: pageIndex,
            pageCount: pageCount
          },
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              this.warningList = this.inspectionList.concat(data.list);
              this.pageIndex = pageIndex;
              this.loading = false;
            }
          }.bind(this)
        });



        /*this.$http.get(this.$api+'/mc/events/list', {
         params:{
         userid: cookie.split('|')[0],
         eventStatus: eventStatus,
         eventType: eventType,
         pageIndex: pageIndex,
         pageCount: pageCount
         }
         }) .then(function (response) {
         console.log(response);
         if(response.data.success == true){
         this.inspectionList = this.inspectionList.concat(response.data.list);
         this.pageIndex = pageIndex;
         }
         }.bind(this)).catch(function(error){
         console.log(error);
         })*/
      },
      goBack(){
        this.$router.push({name: 'main'});
        //this.$router.go(-1);
      }
    }
  }
</script>



