<style scoped>
  html, body, #app{
    height: 100%;
  }
  body{
    display: block;
  }
  .main_content{
    height: 100%;
    display: inline-block;
    width: 100%;
  }
  .main{
    font-size: 14px;
    width: 98%;
    margin: 50px 1% 40px;
   /* height: 100%;
    overflow: hidden;*/
  }
  select {
    width: 100px;
    height: 30px;
    border-radius: 3px;
  }
  i.icon.icon-form-tj {
    width: 39px;
    height: 39px;
    background-image: url(../assets/img/tjtp.svg);
  }
  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }

 /* .main_content>div{
    margin: 10px auto;
  }*/
  .topNav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    z-index: 1010;
    background-color: #26a2ff;
    color: #ffffff;
  }
</style>
<template>
  <div class="main_content" >
    <mt-header :title="getTitle" class="topNav">
      <router-link :to="{name: 'main'}" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="main" v-infinite-scroll="loadMore"

         infinite-scroll-immediate-check="true"
         infinite-scroll-distance="10">
      <!--<div id="scroller" :style="scrollStyle">-->

      <InspectionItem v-for="(item, index) in inspectionList"
                      v-bind:item="item" v-bind:index="index"
                      :key="index">
      </InspectionItem>

    </div>
    <FooterNav></FooterNav>
  </div>
</template>

<script>
  import Vue from 'vue'
  import InspectionItem from './inspection-item.vue';
  import FooterNav from '../components/footer.vue'
  import utils from '../assets/js/utils.js'
  import { Header } from 'mint-ui';
  import { Button } from 'mint-ui';
  Vue.component(Header.name, Header);
  Vue.component(Button.name, Button);
  /*  import iscroll from '../assets/js/iscroll-lite.js'*/
  export default {
    name: 'EventList',
    data(){
      return{
        wrapperH: 0,
        scrollStyle: {
            height: this.wrapperH + 'px'
        },
        pageIndex: this.$route.query.pageIndex,
        pageCount: this.$route.query.pageCount,
        eventStatus: this.$route.params.status,
        eventType: this.$route.params.type,
        totalPage: 0,
        loading: false,
        inspectionList: []
      }
    },
    components: {
      InspectionItem,
      FooterNav
    },
    created(){
      this.getEventsList(this.$route.params.status, this.$route.params.type,
        this.$route.query.pageIndex, this.$route.query.pageCount);
    },
    computed: {
      getTitle:function () {
        if(this.eventType=="responsible"){
          return ""
        }else if(this.eventType=="initiate"){
          return ""
        }else if(this.eventType=="focus"){
          return ""
        }else if(this.eventType=="all") {
            if(this.eventStatus=="all"){
              return ""
            }else if(this.eventStatus=="dealing"){
              return ""
            }else if(this.eventStatus=="done"){
              return ""
            }
        }
      }
    },
    methods:{
        loadMore(){
          if(this.pageIndex < this.totalPage){
            this.getScrollList(this.eventStatus, this.eventType, this.pageIndex+1, this.pageCount);
          }else{
            this.$Message.success('...');
          }
        },
      getEventsList(eventStatus, eventType, pageIndex, pageCount){
        var cookie = utils.getCookie('token');
        if(cookie == null){
          this.$router.push({path: '/login'})
        }
        var token=cookie.split('|')[0];
        $.ajax({
          type : "get",
          url : this.$api+'/mc/events/list',
          data: {
            userid: cookie.split('|')[0],
            eventStatus: eventStatus,
            eventType: eventType,
            pageIndex: pageIndex,
            pageCount: pageCount,
            token:token,
          },
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              this.totalPage = parseInt(data.count/this.pageCount)
                + (data.count%this.pageCount>0? 1: 0);
              this.inspectionList = data.list;
              console.log(JSON.stringify(data))
            }
          }.bind(this)
        });




        /*var cookie = utils.getCookie('token');
        if(cookie == null){
          this.$router.push({path: '/login'})
        }
        this.$http.get(this.$api+'/mc/events/list', {
          params:{
            userid: cookie.split('|')[0],
            eventStatus: eventStatus,
            eventType: eventType,
            pageIndex: pageIndex,
            pageCount: pageCount
          }
        }) .then(function (response) {
          console.log(response);
          if(response.data.success == true){
            this.totalPage = parseInt(response.data.count/this.pageCount)
              + (response.data.count%this.pageCount>0? 1: 0);
            this.inspectionList = response.data.list;
          }
        }.bind(this)).catch(function(error){
          console.log(error);
        })*/
      },
      getScrollList(eventStatus, eventType, pageIndex, pageCount){
        var cookie = utils.getCookie('token');
        if(cookie == null){
          this.$router.push({path: '/login'})
        }
        var token=cookie.split('|')[0];
        this.loading = true;
        $.ajax({
          type : "get",
          url : this.$api+'/mc/events/list',
          data: {
            userid: cookie.split('|')[0],
            eventStatus: eventStatus,
            eventType: eventType,
            pageIndex: pageIndex,
            pageCount: pageCount,
            token:token
          },
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              this.inspectionList = this.inspectionList.concat(data.list);
              this.pageIndex = pageIndex;
              this.loading = false;
            }
          }.bind(this)
        });



        /*this.$http.get(this.$api+'/mc/events/list', {
          params:{
            userid: cookie.split('|')[0],
            eventStatus: eventStatus,
            eventType: eventType,
            pageIndex: pageIndex,
            pageCount: pageCount
          }
        }) .then(function (response) {
          console.log(response);
          if(response.data.success == true){
            this.inspectionList = this.inspectionList.concat(response.data.list);
            this.pageIndex = pageIndex;
          }
        }.bind(this)).catch(function(error){
          console.log(error);
        })*/
      },
      goBack(){
        this.$router.push({name: 'main'});
        //this.$router.go(-1);
      }
    }
  }
</script>



