<style scoped>
  html{
    font-size: 62.5%;
  }
  .ivu-card-bordered {
    border: 0px solid #fff;
  }
  i.icon.icon-form-org {
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/lct.svg);
  }
  i.icon.icon-form-name {
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/tx.svg);
  }
  i.icon.icon-form-suo {
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/suo.svg);
  }
  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }
  .red {
    color: crimson
  }
  .app_title{
    font-size: 1.4rem;
    height: 177px;
    background: #fff url(../assets/img/top_logo.png) repeat-x;
    background-size: auto 177px;
  }
  .ivu-card{
    width: 100%;
  }
  .main{
    font-size: 1.6rem;
  }
  </style>
<template>
  <div class="main">
    <Row class="app_title">
      <div style="margin-top: 80px;color: #fff"><strong>CDC</strong></div>
      <div style="margin-top: 5px;font-size: 1.0rem;text-shadow:0 0 0.2em #fff, -0 -0 0.2em #fff;">
        C<span style="color: red">D</span>C <span style="color: blue">S</span>ecurity management platform</div>
    </Row>
    <Card>
      <Form :model="formInline">
        <Form-item>
          <Row>
            <Col span="3"><i class="icon icon-form-org "></i></Col>
            <Col span="21" style="text-align: left">
            <i-select v-model="formInline.orgid" placeholder="" @on-change="getStaffList">
            <i-option value=""></i-option>
            <i-option v-for="item in orgList" :value="item.id" :key="item.id">{{ item.name }}</i-option>
          </i-select></Col>
          </Row>
        </Form-item>
        <Form-item>
          <Row>
            <Col span="3"><i class="icon icon-form-name"></i></Col>
            <Col span="21">
              <i-select v-model="formInline.staffid" style="text-align: left" placeholder="">
                <i-option value=""></i-option>
                <i-option v-for="item in staffList" :value="item.id" :key="item.id">[{{item.jobnumber}}]{{ item.name }}</i-option>
              </i-select>
             </Col>
          </Row>
        </Form-item>
        <Form-item>
          <Row>
            <Col span="3"><i class="icon icon-form-suo"></i></Col>
            <Col span="21"><Input type="password"  v-model="formInline.password" placeholder=""></Input></Col>
          </Row>
        </Form-item>
        <Form-item>
          <Row>
            <Col span="3">&nbsp;<span v-if="validateSuccess == 1"><Icon type="checkmark-round" size="16" color="green"></Icon></span>
              <span v-else-if="validateSuccess == -1"><Icon size="16" type="close-round" color="red"></Icon></span>
            </Col>
            <Col span="15">
             <Input  v-model="formInline.captcha" @on-change="verifyCaptcha" placeholder="" :maxlength="4"></Input>
            </Col>
            <Col span="6">
              <img :src="$api+'/mc/captcha?rnd='+rndNum" @click="refreshCaptcha">
            </Col>
          </Row>
        </Form-item>
        <Form-item>
          <Button type="primary" size="large"  @click.native="login" style="width: 100%;height:40px;"></Button>
        </Form-item>
      </Form>
    </Card>
  </div>
</template>

<script>
  import '@/assets/css/iconfont.css'
  import utils from '../assets/js/utils.js'
  export default {
    data () {
      return {
        orgList: [],
        orgid: null,
        staffList: [],
        errormsg: null,
        validateSuccess: 0,
        rndNum : Math.random(),
        formInline: {
          orgid: null,
          captcha: null,
          staffid: null,
          password: null
        },

        formValidate: {
          staffid: '',
          orgid: '',
          password: '',
          captcha: ''
        },
        ruleValidate: {
          orgid: [
            { required: true, message: '', trigger: 'blur' }
          ],
          staffid: [
            { required: true, message: '', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '', trigger: 'blur' }
          ],
          captcha: [
            { required: true, message: '', trigger: 'change' }
          ]
        }
      }
    },
    mounted(){
      var token = utils.getCookie('token');
      if(token != null){
        this.$router.push({name: 'main'});
      }
      this.getOrgnizationList();
    },
    methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('!');
          } else {
            this.$Message.error('!');
          }
        })
      },

      refreshCaptcha(){
          this.rndNum = Math.random();
      },
      verifyCaptcha(){
        if(this.formInline.captcha.length == 4){
          $.ajax({
            type : "get",
            url : this.$api+'/mc/captcha/verify?checkCode='+this.formInline.captcha,
            dataType : "json",
            success : function(data) {
              if(data.success == true){
                this.validateSuccess = 1;
              }else{
                this.validateSuccess = -1;
              }
            }.bind(this)
          });


         /* this.$http.get(this.$api+'/mc/captcha/verify?checkCode='+this.captcha)
            .then(function (response) {
              if(response.data.success == true){
                this.validateSuccess = 1;
              }else{
                this.validateSuccess = -1;
              }
            }.bind(this)).catch(function(error){
            console.log(error);
          })*/
        }
      },
      login(){
        var _this = this;
        $.ajax({
          type : "post",
          url : this.$api+'/mc/login',
          data: this.formInline,
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              console.log(data)
              var token = data.obj.id + '|' + data.obj.jobnumber +
                '|' + encodeURI(data.obj.name);

              var userToken = data.obj.id + '|' + data.obj.mobile +
                '|' + data.obj.roleType;
              utils.addCookie('token', token, 0);
              utils.addCookie("userinfo", JSON.stringify(data.obj));
              utils.addCookie('userToken', userToken, 0);

              var token2 = utils.getCookie('token');
              this.$router.push({name: 'main'});
            }else{
              this.$Message.error(data.msg);
              this.rndNum = Math.random();
            }
          }.bind(this)
        });
      },
      getStaffList(){
        var _this = this;
        console.log('orgid='+this.formInline.orgid)
        $.ajax({
          type : "get",
          url : this.$api+'/staff',
          data: 'orgid='+this.formInline.orgid,
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              _this.staffList = data.list;
            }
          }
        });
      },
      getOrgnizationList(){
        var _this = this;
        $.ajax({
          type : "get",
          url : this.$api+'/organization/find',
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              _this.orgList = data.list;
            }
          }
        });
      }
    }
  }
</script>



