<style scoped>
  .layout-content {
    background-color: rgb(247, 247, 247);
    height: 100%;
  }

  .main {
    font-size: 14px;
    width: 98%;
    margin: 60px 1%;
  }

  select {
    width: 100px;
    height: 30px;
    border-radius: 3px;
  }

  i.icon.icon-form-tj {
    width: 39px;
    height: 39px;
    background-image: url(../assets/img/tjtp.svg);
  }

  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }

  /* .main_content>div{
      margin: 10px auto;
   }*/
  .tips {
    margin-left: 60px;
    text-align: left;
    line-height: 20px;
    color: #EE0000;
  }

  .topNav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    z-index: 1010;
    background-color: #26a2ff;
    color: #ffffff;
  }

  a {
    text-decoration: none;
  }

  .am-navbar {
    font-size: 14px;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    z-index: 1010;
    border-top: 1px solid #b8b8b8;
  }

  .am-navbar .am-navbar-nav {
    padding-left: 8px;
    padding-right: 8px;
    text-align: center;
    overflow: hidden;
  }

  .am-navbar ul {
    padding-left: 0;
    margin: 0;
    list-style: none;
    width: 100%;
  }

  .am-navbar-nav {
    position: relative;
    z-index: 1015;
  }

  .am-navbar .am-navbar-nav li {
    display: table-cell;
    width: 1%;
    float: none;
    cursor: pointer;
  }

  .am-navbar-nav li:not(:last-child) {
    border-right: 1px solid #b8b8b8;
  }

  .am-avg-sm-4 > li:nth-of-type(4n+1) {
    clear: both;
  }

  .am-avg-sm-4 > li:nth-of-type(n) {
    clear: none;
  }

  .am-avg-sm-4 > li {
    width: 25%;
  }

  [class*=am-avg-] > li {
    display: block;
    height: auto;
    float: left;
  }

  .object_name {
    position: absolute;
    top: 3px;
    left: 6px;
    height: 29px;
    width: 80%;
    border: 0;
    outline: none;
  }

</style>
<template>
  <div class="layout-content-main">
    <mt-header title="" class="topNav">
      <router-link to="/" slot="left">
        <mt-button icon="back" @click="goBack"></mt-button>
      </router-link>
    </mt-header>
    <div class="main">
      <Form style="width: 100%;overflow-x: hidden">
        <Form-item label="">
          <Row>
            <Col span="4">
            </Col>
            <Col span="7" style="text-align: left">
            <i-select placeholder="" v-model="buildingIndex" @on-change="getFloorList">
              <i-option :value="index" v-for="(item, index) in buildingList" :key="item.id">
                {{item.name}}
              </i-option>
            </i-select>
            </Col>
            <Col span="7" style="text-align: left">
            <i-select placeholder="" v-model="formParams.floorno" @on-change="getRoomList">
              <i-option :value="item" v-for="(item, index) in floorList" :key="index">
                {{item}}
              </i-option>
            </i-select>
            </Col>
            <Col span="6" style="text-align: left">
            <i-select placeholder="" v-model="formParams.roomid">
              <i-option :value="item.id" v-for="(item, index) in roomList" :key="item.id">
                {{item.roomno}}
              </i-option>
            </i-select>
            </Col>
          </Row>
        </Form-item>
        <Form-item label="">
          <Row>
            <Col span="4">
            </Col>
            <Col span="20" style="text-align: left">
            <Select name="mac-windows" v-model="formParams.type" placeholder="" @on-change="getTargetList">
              <Option value="1001"></Option>
              <Option value="1002"></Option>
              <Option value="1003"></Option>
              <!--<Option value="1004"></Option>-->
              <Option value="1000"></Option>
            </Select>
            </Col>
          </Row>
        </Form-item>
        <Form-item label="">
          <Row>
            <Col span="4">
            </Col>
            <Col span="20" style="text-align: left">
            <i-select  v-model="formParams.target" @on-change="getTarget" filterable>
              <i-option :value="item.id" v-for="(item, index) in targetList" :key="item.id">
                {{item.name}}
              </i-option>
            </i-select>
            <input type="text" @change="inputChange" placeholder="" class="object_name" v-model="objectName"/>
            </Col>
          </Row>
        </Form-item>
        <Form-item label="">
          <Row>
            <Col span="4">
            </Col>
            <Col span="8" style="text-align: left">
            <Select name="type" :disabled="isDisabled" v-model="formParams.riskGrade" placeholder=""
                    ref="selectChange">
              <Option value="0"></Option>
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
              <Option value="4">4</Option>
            </Select>
            </Col>
            <Col span="4">&nbsp;&nbsp;</Col>
            <Col span="8" style="text-align: left">
            <Select v-model="formParams.riskType" placeholder="">
              <Option value="3001"></Option>
              <Option value="3002"></Option>
              <Option value="3003"></Option>
              <Option value="3004"></Option>
              <Option value="3005"></Option>
            </Select>
            </Col>
          </Row>
          <div class="tips">
            <div v-if="formParams.riskGrade=='0'">{{riskTip.g2002}}</div>
            <div v-else-if="formParams.riskGrade=='1'">{{riskTip.g2012}}</div>
            <div v-else-if="formParams.riskGrade=='2'">{{riskTip.g2022}}</div>
            <div v-else-if="formParams.riskGrade=='3'">{{riskTip.g2032}}</div>
            <div v-else-if="formParams.riskGrade=='4'">{{riskTip.g2042}}</div>
            <div v-else></div>
          </div>
        </Form-item>

        <Form-item label="">
          <Row>
            <Col span="4">
            </Col>
            <Col span="20">
            <Input v-model="formParams.title" placeholder=""></Input>
            </Col>
          </Row>
        </Form-item>
        <Form-item label="">
          <Row>
            <Col span="4">
            </Col>
            <Col span="20">
            <Input v-model="formParams.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                   placeholder="..."></Input></Col>
          </Row>
        </Form-item>
        <Form-item label="">
          <Row>
            <Col span="4">
            </Col>
            <Col span="20" style="text-align: left">
            <i class="icon icon-form-tj" @click="loadImageUpload"></i> </Col>
          </Row>
        </Form-item>
        <Form-item>
          <Row :gutter="16">
            <Col span="24">
            <img v-for="url in imgUrlList" :src="url" style="width: 70px; height: 70px">
            </Col>
          </Row>
        </Form-item>
      </Form>
      <div style="margin-top: 50px;"></div>
    </div>

    <div data-am-widget="navbar" class="am-navbar am-cf am-navbar-default am-no-layout" id="">
      <ul class="am-navbar-nav am-cf am-avg-sm-4">
        <li @click="saveAsDraft">
          <span class="am-navbar-label"></span>
        </li>
        <li @click="createEvent">
          <span class="am-navbar-label"></span>
        </li>
      </ul>
      <ul class="am-navbar-actions"></ul>
    </div>
  </div>
</template>

<script>
  import Qs from 'qs'
  import Utils from '../assets/js/utils'
  import Vue from 'vue'
  import {Header} from 'mint-ui';
  import utils from '../assets/js/utils.js';
  import Input from "iview/src/components/input/input";

  Vue.component(Header.name, Header);

  export default {
    components: {Input},
    name: 'EventSubmit',
    data() {
      return {
        uploadList: [],
        imgSrc: null,
        errorMsg: null,
        cookie: null,
        formParams: {
          inspector: null,
          roomid: null,
          type: null,
          grade: null,
          riskType: null,
          riskGrade: null,
          title: null,
          desc: null,
          ipicture: null,
          target: null,
          buildingid: null,
          floorno: null,
          roomno: null,
        },
        targetType: null,
        buildingIndex: null,
        floorno: null,
        buildingList: [],
        floorList: [],
        roomList: [],
        imgUrlList: [],
        targetList: [],
        url_arr: [],
        riskTip: {
          g2002: null,
          g2012: null,
          g2022: null,
          g2032: null,
          g2042: null
        },
        isDisabled: false,
        objectName: null,
      }
    },
    mounted() {
      this.getBuildingList();
      this.getRiskTiop();
      // this.options = this.setOptions( Camera.PictureSourceType.CAMERA);
    },
    methods: {
      saveAsDraft() {
        var cookie = utils.getCookie('token'),
          eventLocalSt = localStorage.getItem('eventDraft'),
          eventList = eventLocalSt != null ? JSON.parse(eventLocalSt) : new Array();
        if (cookie == null) {
          this.$Message.error('');
          return;
        }
        this.formParams.inspector = cookie.split('|')[0];

        eventList.push(this.formParams);
        localStorage.setItem('eventDraft', JSON.stringify(eventList));
        this.$Message.success('');
      },
      createEvent() {
        var token = Utils.getCookie('token');
        if (token == null) {
          this.$Message.error("");
          this.$router.push({name: 'login'});
        }
        this.formParams.inspector = token.split('|')[0];
        this.formParams.token = token.split('|')[0];
        console.log(JSON.stringify(this.formParams))
        $.ajax({
          type: "post",
          url: this.$api + '/inspectevent/add',
          data: Qs.stringify(this.formParams),
          dataType: "json",
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          success: function (data) {
            if (data.success == true) {
              this.$Message.success("");
              this.$router.push({
                name: 'list', params: {status: 'dealing', type: 'initiate'},
                query: {pageIndex: 1, pageCount: 6}
              })
//              this.$router.push({name: 'list'});
            } else {
              this.$Message.error(data.msg);
            }
          }.bind(this)
        });
      },
      inputChange() {
        this.formParams.target=null;
        this.isDisabled = false;
        this.formParams.riskGrade = null;
      },
      getTargetList() {
        if (this.formParams.type == '1001') {
          this.getInstrumentList();
        }
        if (this.formParams.type == '1002') {
          this.getBiologicalList();
        }
        if (this.formParams.type == '1003') {
          this.getChemicalList();
        }
      },
      getTarget() {
        for (var i = 0; i < this.targetList.length; i++) {
          if (this.targetList[i].id == this.formParams.target) {
            this.objectName = this.targetList[i].name;
          }
          if (this.targetList[i].id == this.formParams.target && this.targetList[i].riskGrade != undefined) {
            this.formParams.riskGrade = this.targetList[i].riskGrade + "";
            this.isDisabled = true;
          }
          if (this.targetList[i].id == this.formParams.target && this.targetList[i].riskGrade == undefined) {
            this.isDisabled = false;
            this.formParams.riskGrade = "";
          }
        }


      },
      //
      getInstrumentList() {
        var cookie = utils.getCookie('token');
        if (cookie == null) {
          this.$router.push({path: '/login'})
        }
        ;
        var token = cookie.split('|')[0];
        var userToken = utils.getCookie('userToken');

        $.ajax({
          type: "get",
          url: this.$api + '/instrument/find?floorno=' + this.formParams.floorno + '&roomid=' + this.formParams.roomid + '&token=' + encodeURI(userToken),
          dataType: "json",
          success: function (data) {
            if (data.success == true) {
              console.log("data:          " + JSON.stringify(data));
              this.targetList = data.list;
            } else {
              this.$Message.error('');
            }
          }.bind(this)
        });
      },
      //
      getBiologicalList() {
        var cookie = utils.getCookie('token');
        if (cookie == null) {
          this.$router.push({path: '/login'})
        }
        ;
        var token = cookie.split('|')[0];

        $.ajax({
          type: "get",
          url: this.$api + '/biological/find?token=' + token,
          dataType: "json",
          success: function (data) {
            if (data.success == true) {
              this.targetList = data.list;
            } else {
              this.$Message.error('');
            }
          }.bind(this)
        });
      },
      getChemicalList() {
        var cookie = utils.getCookie('token');
        if (cookie == null) {
          this.$router.push({path: '/login'})
        }
        ;
        var token = cookie.split('|')[0];
        $.ajax({
          type: "get",
          url: this.$api + '/chemistry/find?token=' + token,
          dataType: "json",
          success: function (data) {
            if (data.success == true) {
              this.targetList = data.list;
            } else {
              this.$Message.error('');
            }
          }.bind(this)
        });
      },
      getFloorList() {
        this.formParams.buildingid = this.buildingList[this.buildingIndex].id;
        var floorCount = this.buildingList[this.buildingIndex].levelscount;
        var arr = [];
        for (var i = 0; i < floorCount; i++) {
          arr.push(i + 1);
        }
        this.floorList = arr;
      },
      getRiskTiop() {
        var this_ = this;
        var userToken = utils.getCookie('userToken');
        $.ajax({
          type: "get",
          url: this.$api + '/role/eventGradeInfo?token=' + encodeURI(userToken),
          dataType: "json",
          success: function (data) {
            console.log(data)
            if (data.success == true) {

              this_.riskTip.g2002 = data.obj.g2002;
              this_.riskTip.g2012 = data.obj.g2012;
              this_.riskTip.g2022 = data.obj.g2022;
              this_.riskTip.g2032 = data.obj.g2032;
              this_.riskTip.g2042 = data.obj.g2042;
            }
          }.bind(this)
        });
      },
      getBuildingList() {
        this.cookie = utils.getCookie('token');
        if (this.cookie == null) {
          this.$router.push({path: '/login'})
        }
        var token = this.cookie.split('|')[0];
        $.ajax({
          type: "get",
          url: this.$api + '/building/find?token=' + token,
          dataType: "json",
          success: function (data) {
            if (data.success == true) {
              this.buildingList = data.list;
            } else {
              this.$Message.error('');
            }
          }.bind(this)
        });
      },
      getRoomList() {
        var cookie = utils.getCookie('token');
        if (cookie == null) {
          this.$router.push({path: '/login'})
        }
        var token = cookie.split('|')[0];
        $.ajax({
          type: "get",
          url: this.$api + '/room/find?token=' + token,
          data: {
            buildingId: this.formParams.buildingid,
            floorno: this.formParams.floorno
          },
          dataType: "json",
          success: function (data) {

            if (data.success == true) {
              this.roomList = data.list;
            } else {
              this.$Message.error('');
            }
          }.bind(this)
        });
      },
      goBack() {
        this.$router.go(-1);
      },
      setOptions(srcType) {
        var options = {
          // Some common settings are 20, 50, and 100
          quality: 50,
          // destinationType: Camera.DestinationType.FILE_URI,
          destinationType: Camera.DestinationType.DATA_URL,
          // In this app, dynamically set the picture source, Camera or photo gallery
          sourceType: srcType,
          encodingType: Camera.EncodingType.JPEG,
          mediaType: Camera.MediaType.PICTURE,
          allowEdit: true,
          correctOrientation: true  //Corrects Android orientation quirks
        }
        return options;
      },
      openCamera() {
        navigator.camera.getPicture(this.onLoadImageSuccess, this.onLoadImageFail, {destinationType: Camera.DestinationType.DATA_URL});
      },
      //
      loadImageUpload() {
        var _this = this;
        api.getPicture({
          sourceType: 'camera',
          encodingType: 'jpg',
          mediaValue: 'pic',
          destinationType: 'url',
          allowEdit: true,
          quality: 50,
          saveToPhotoAlbum: false
        }, function (ret, err) {
          var cookie = utils.getCookie('token');
          var token = cookie.split('|')[0];
          var imgPath = ret.data;
          api.showProgress({
            title: '...',
            modal: false
          });
          api.ajax({
            url: _this.$api + '/image/upload?token=' + token,
            method: 'post',
            data: {
              files: {
                file: imgPath
              }
            }
          }, function (ret, err) {
            api.hideProgress();
            if (ret) {
              var url = ret.obj.url;
              _this.imgUrlList.push(_this.$apit + "/images" + url);
              _this.url_arr.push(url)
              _this.formParams.ipicture = _this.url_arr.join();
              console.log(_this.$apit + "/images" + url)
            } else {
              alert(JSON.stringify(err));
            }
          });

        })

        //
        // navigator.camera.getPicture(this.onLoadImageUploadSuccess, this.onLoadImageFail, {
        //   destinationType: Camera.DestinationType.FILE_URI
        // });

      },
      //
      onLoadImageUploadSuccess(imageURI) {
        var _this = this;
        var cookie = utils.getCookie('token');
        var token = cookie.split('|')[0];
        //
        var options = new FileUploadOptions(); //
        options.fileKey = "file";//fileparameter name
        options.fileName = imageURI.substr(imageURI.lastIndexOf('/') + 1);//
        options.mimeType = "image/jpeg";//image/jpeg
        options.chunkedMode = false;

        var params = {};
        params.token = token;
        options.params = params;
        var ft = new FileTransfer();//
        ft.onprogress = function (progressEvt) {//
          if (progressEvt.lengthComputable) {
            navigator.notification.progressValue(Math.round((progressEvt.loaded / progressEvt.total) * 100));
          }
        }
        navigator.notification.progressStart("", "");
        ft.upload(imageURI, encodeURI(this.$api + '/image/upload'), function (data) {
          navigator.notification.progressStop();//
          _this.imgUrlList.push(imageURI);
          data.response = eval('(' + data.response + ')');
          _this.formParams.ipicture = data.response.obj.url;
          navigator.notification.alert("", null, "");
        }, _this.uploadFail, options, true);
      },
      uploadFail(error) {
        navigator.notification.alert("" + error.code + "" + message, null, "");
      },
      onLoadImageSuccess(imageURI) {
        //base64
        var src = "data:image/jpeg;base64," + imageURI;
        // $("#getImage").attr("src", src);
        // $("#getImage").show();
      },
      //
      onLoadImageFail(message) {
        navigator.notification.alert("" + message, null, "");
      }

    }
  }
</script>



