    -moz-box-shadow: 0px 1px 1px #888888;
    box-shadow: 0px 0.5px 1px #888888;
  }
  i.icon.icon-form-logo {
    width: 29px;
    height: 29px;
    margin-top: 12px;
    background-image: url(../assets/img/logo.svg);
  }
  i.icon.icon-form-dt{
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/dt.svg);
  }
  i.icon.icon-form-duihao{
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/duihao.svg);
  }
  i.icon.icon-form-zhuyi{
    width: 29px;
