<template>
    <!-- Your main view, should have "main" prop -->
    <div class="layout-content-main">
      <div style=" background-color: rgb(34,144,237); color: #fff;font-size: 18px"><strong>CDC</strong></div>
      <div class="user_info topNav">
        <Row style="height: 50px;line-height: 50px;font-size: 14px">
          <Col span="8" @click.native="$router.push({name: 'warnings'})">&nbsp;
          <Icon type="ios-bell-outline" style="font-size: 1.5rem;cursor: pointer" ></Icon></Col>
          <Col span="13">{{jobnumber}}&nbsp;{{name}}</Col>
          <Col span="2"  @click.native="modal1 = true"></Col>
        </Row>
        <Modal
          v-model="modal1"
          title=""
          @on-ok="ok"
          @on-cancel="cancel">
          <p  style="text-align: center; font-size: 1.5em"></p>
        </Modal>
        <Row class="total">
          <Col span="6" @click.native="showEventsList('all', 'all', 1, 6)">
          <div class="t_div"><span>{{eventType.total}}</span></div><div class="t_div"></div>
          </Col>
          <Col span="6" @click.native="showEventsList('dealing', 'all', 1, 6)">
          <div class="t_div"><span>{{eventType.dealing}}</span></div><div class="t_div"></div></Col>
          <Col span="6" @click.native="showEventsList('done', 'all', 1, 6)">
          <div class="t_div"><span>{{eventType.done}}</span></div><div class="t_div"></div>
          </Col>
          <Col span="6"><router-link :to="{name: 'draft'}">
          <div class="t_div"><span>{{draftNum}}</span></div><div class="t_div"></div></router-link></Col>
        </Row>
      </div>
      <div class="main">
        <div style="margin-top: 10px;"></div>
      <div>
            <div class="item-media">
              <Row>
                <Col span="4" style="text-align: center"><img src="../assets/img/overtime.png"></Col>
                <Col span="20"></Col>
              </Row>
            </div>
            <div class="eventNum">
              <div  @click="showEventsList('overtime', 'responsible', 1, 6)">
                &nbsp;<span>{{statistic.overtime.responsible.length}}</span></div>
              <div @click="showEventsList('overtime', 'initiate', 1, 6)">
                &nbsp;<span>{{statistic.overtime.initiate.length}}</span></div>
              <div @click="showEventsList('overtime', 'focus', 1, 6)">
                &nbsp;<span>{{statistic.overtime.focus.length}}</span></div>
            </div>
      </div>
      <div>
            <div class="item-media">
              <Row>
                <Col span="4" style="text-align: center"><img src="../assets/img/dealing.png"></Col>
                <Col span="20"></Col>
              </Row>
            </div>
            <div class="eventNum">
              <div @click="showEventsList('dealing', 'responsible', 1, 6)">
                &nbsp;<span>{{statistic.dealing.responsible.length}}</span></div>
              <div @click="showEventsList('dealing', 'initiate', 1, 6)">
                &nbsp;<span>{{statistic.dealing.initiate.length}}</span></div>
              <div  @click="showEventsList('dealing', 'focus', 1, 6)">
                &nbsp;<span>{{statistic.dealing.focus.length}}</span></div>
            </div>
      </div>
      <div>
        <div class="item-media">
          <Row>
            <Col span="4" style="text-align: center"><img src="../assets/img/done.png"></Col>
            <Col span="20"></Col>
          </Row>
        </div>
            <div class="eventNum">
              <div  @click="showEventsList('done', 'responsible', 1, 6)">
                &nbsp;<span>{{statistic.done.responsible.length}}</span></div>
              <div  @click="showEventsList('done', 'initiate', 1, 6)">
                &nbsp;<span>{{statistic.done.initiate.length}}</span></div>
              <div  @click="showEventsList('done', 'focus', 1, 6)">
                &nbsp;<span>{{statistic.done.focus.length}}</span></div>
            </div>
      </div>
      <div>
          <div class="item-media">
            <Row>
              <Col span="4" style="text-align: center"><img src="../assets/img/message.png"></Col>
              <Col span="10"></Col>
              <Col  span="10" style=" display: none; text-align: center">&nbsp;&nbsp;&nbsp;
              <router-link style="display: none"  :to="{path: '/list'}"></router-link></Col>
            </Row>
          </div>
        <div>
          <InspectionItem v-for="(item, index) in inspectionList"
                          v-bind:item="item" v-bind:index="index"
                          :key="index">
          </InspectionItem>
        </div>
      </div>
      <div style="margin-top: 50px;"></div>
      <FooterNav></FooterNav>
      </div>
    </div>
</template>


<script>
  import InspectionItem from './inspection-item.vue';
  import FooterNav from '../components/footer.vue';
  import utils from '../assets/js/utils.js';
export default {
      name: "Main",
      components: {
        InspectionItem,
        FooterNav
      },
      data(){
          return {
            modal1: false,
            jobnumber: null,
            userid: null,
            name: null,
            draftNum: 0,
            allEventsList: [],
            inspectionList: [],
            eventType: {
              total: 0,
              dealing: 0,
              done: 0
            },
            statistic: {
              overtime: {responsible: [], initiate: [], focus: []},
              dealing: {responsible: [], initiate: [], focus: []},
              done: {responsible: [], initiate: [], focus: []}
            }
          }
      },
      mounted(){
        this.getUserInfo();
        this.getDraftNums();
        this.getInspectEventType();
        this.getEventsStatistic();
        //this.showAllEventsList();
      },
      methods: {
        getUserInfo(){
          var cookie = utils.getCookie('token');
          if(cookie != null){
              var arr = cookie.split('|');
              this.userid = arr[0];
              this.jobnumber = arr[1];
              this.name = decodeURI(arr[2]);
          }
        },
        getInspectEventType(){
          var cookie = utils.getCookie('token');
          if(cookie == null){
            this.$router.push({path: '/login'})
          }
          var userid=cookie.split('|')[0];
          var userToken = utils.getCookie('userToken');

          console.log("main.vue   token     "+userToken);
          $.ajax({
            type : "get",
            url : this.$api+'/mc/event/type?userid='+userid +'&'+'token='+userid,
            dataType : "json",
            success : function(data) {
              if(data.success == true){
                this.eventType = data.obj;
              }
            }.bind(this)
          });
        },
        getEventsStatistic(){
          var cookie = utils.getCookie('token');
          if(cookie == null){
              this.$router.push({path: '/login'})
          };
          var token=cookie.split('|')[0];
          $.ajax({
            type : "get",
            url : this.$api+'/mc/events/statistic?userid='+token +'&'+'token='+token,
            dataType : "json",
            success : function(data) {
              if(data.success == true){
                this.statistic = data.obj;
                this.showAllEventsList(this.statistic);
              }
            }.bind(this)
          });
        },
        showAllEventsList(statistic){
            for(var i in statistic){
                for(var j in statistic[i]){
                   if(statistic[i][j].length > 0){
                     for(var k in statistic[i][j]){
                       if(statistic[i][j][k] != null){
                         this.allEventsList.push(statistic[i][j][k]);
                       }
                     }
                   }
                }
            }
//          console.log(JSON.stringify(this.allEventsList))
          //5
          this.inspectionList = [];
           var listLen = 5 < this.allEventsList.length? 5 : this.allEventsList.length;
           for(var i=0; i< listLen; i++){
                this.inspectionList.push(this.allEventsList[i]);
           }
          // console.log('inspectionList:'+JSON.stringify(this.inspectionList))
        },
        ok(){
          utils.delCookie("token");
          this.$router.push({path: '/login'})
        },
        cancel(){
        },
        showEventsList(type, name, pageIndex, pageCount){
            this.$router.push({name: 'list', params:{status: type, type: name},
              query: {pageIndex: pageIndex, pageCount: pageCount}})
            /*this.inspectionList = this.statistic[type][name];*/
        },
        getDraftNums(){
          var eventLocalSt = localStorage.getItem('eventDraft'),
            eventList = eventLocalSt != null? JSON.parse(eventLocalSt): new Array();
          this.draftNum = eventList.length;
        }
      }
}
</script>



