import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/login'
import Main from '@/pages/main'
import Report from '@/pages/event-report.vue'
import EventDetail from '@/pages/event-detail.vue'
import EventList from '@/pages/event-list.vue'
import DraftList from '@/pages/draft-list.vue'
import Warnings from '@/pages/warnings.vue'
import Register from '@/pages/done-registration.vue'
import Focus from '@/components/person-focus.vue'
import AddPerson from '@/components/addPerson.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Login
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/main',
      name: 'main',

      /*meta:{
       requireAuth: true //token
      },*/
      component: Main
    },
    {
      path: '/warnings',
      name: 'warnings',
      component: Warnings
    },
    {
      path: '/report',
      name: 'report',
      /*meta:{
        requireAuth: true //token
      },*/
      component: Report
    },
    {
      path: '/detail/:roomId/:id/:targetid/:eventGrade',
      name: 'detail',
      component: EventDetail
    },
    {
      path: '/detail/:orgid/:riskGrade/focus',
      name: 'focus',
      component: Focus
    },
    {
      path: '/list/:status/:type/',
      name: 'list',
      component: EventList
    },
    {
      path: '/list/draft',
      name: 'draft',
      component: DraftList
    },
    {
      path: '/event/register',
      name: 'register',
      component: Register
    },
    {
      path:'/addpersom',
      name:'addpersom',
      component:AddPerson
    }
  ]
})



