<style scoped>
  .main{
    font-size: 14px;
    width: 98%;
    margin: 50px 1%;
  }
  select {
    width: 100px;
    height: 30px;
    border-radius: 3px;
  }
  i.icon.icon-form-tj {
    width: 39px;
    height: 39px;
    background-image: url(../assets/img/tjtp.svg);
  }
  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }

  .main_content>div{
    margin: 10px auto;
  }
  .topNav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    z-index: 1010;
    background-color: #26a2ff;
    color: #ffffff;
  }
  a{
    text-decoration: none;
  }
  .am-navbar {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    z-index: 1010;
    color: #ffffff;
  }
  .am-navbar-default .am-navbar-nav {
    background-color: #26a2ff;
  }
  .am-navbar .am-navbar-nav {
    padding-left: 8px;
    padding-right: 8px;
    text-align: center;
    overflow: hidden;
  }
  .am-navbar ul {
    padding-left: 0;
    margin: 0;
    list-style: none;
    width: 100%;
  }

  .am-navbar-nav {
    position: relative;
    z-index: 1015;
  }

  .am-navbar .am-navbar-nav li {
    display: table-cell;
    width: 1%;
    float: none;
    cursor: pointer;
  }
  .am-navbar-nav li:not(:last-child){
    border-right: 1px solid #b8b8b8;
  }
  .am-avg-sm-4>li:nth-of-type(4n+1) {
    clear: both;
  }

  .am-avg-sm-4>li:nth-of-type(n) {
    clear: none;
  }
  .am-avg-sm-4>li {
    width: 25%;
  }
  [class*=am-avg-]>li {
    display: block;
    height: auto;
    float: left;
  }
  .item{
    border-bottom: 1px solid #8EBEC4;
    font-size: 14px;
    text-align: left;
  }
  .item>div{
    width: 100%;
  }
</style>
<template>
  <div class="layout-content-main" >
    <mt-header title="" class="topNav">
      <router-link to="/" slot="left">
        <mt-button icon="back" @click="goBack"></mt-button>
      </router-link>
    </mt-header>
    <div class="main">
      <Checkbox-group v-model="selectedList">
      <div v-for="(item, index) in draftList" class="item">
        <Row :gutter="16">
          <Col span="22">
          <Row :gutter="32">
            <Col span="14"><strong></strong>&nbsp;{{item.buildingid}}
            &nbsp;&nbsp;&nbsp;{{item.floorno}}
            &nbsp;&nbsp;&nbsp;{{item.roomno}}</Col>
            <Col span="10"><strong></strong>{{getTargetName(item)}}</Col>
          </Row>
          <Row :gutter="32">
            <Col span="14"><strong></strong>{{item.targetCode}}</Col>
            <Col span="10"><strong></strong>{{item.type}}</Col>
          </Row>
          <Row :gutter="32">
            <Col span="14"><strong></strong><span>{{item.title}}</span>&nbsp;&nbsp;</Col>
            <Col span="10"><strong></strong><span>{{getRiskGrade(item)}}</span></Col>
          </Row>
          <Row :gutter="32">
            <Col span="24"><strong></strong><span>{{item.desc}}</span>&nbsp;&nbsp;</Col>
          </Row>
          </Col>
          <Col span="2"><Checkbox  :label="index">&nbsp;</Checkbox></Col>
        </Row>
      </div>
      </Checkbox-group>

      <div data-am-widget="navbar" class="am-navbar am-cf am-navbar-default am-no-layout" id="">
        <ul class="am-navbar-nav am-cf am-avg-sm-4">
          <li @click="deleteDraft">
            <span class="am-navbar-label"></span>
          </li>
          <li @click="createEvent">
            <span class="am-navbar-label"></span>
          </li>
        </ul>
        <ul class="am-navbar-actions"></ul>
      </div>
    </div>
  </div>
</template>

<script>
  import FooterNav from '../components/footer.vue'
  import utils from '../assets/js/utils.js'
  import Qs from 'qs'
  import Vue from 'vue'
  import { Header } from 'mint-ui';

  Vue.component(Header.name, Header);

  export default {
    name: 'DraftList',
    data(){
      return{
        isChecked: false,
        draftList: [],
        selectedList: [],
        targetName: {
          '1001': '',
          '1002': '',
          '1003': '',
          '1004': '',
        }
      }
    },
    props: ['item'],
    components: {
      FooterNav
    },

    mounted(){
      var eventLocalSt = localStorage.getItem('eventDraft'),
           eventList = eventLocalSt != null? JSON.parse(eventLocalSt): new Array();
      this.draftList = eventList;
    },
    methods:{
      getTargetName(item){
         return this.targetName[item.target];
       },
      getRiskGrade(item){
        if(item.grade == '3'){
          return '';
        }else if(item.grade == '2'){
          return '';
        }else{
          return '';
        }
      },
      goBack(){
        this.$router.go(-1);
      },
      createEvent(){
        var cookie = utils.getCookie('token');
        if(cookie == null){
          this.$Message.error('');
          return;
        }
        for(var idx in this.selectedList){
            var eventObj = this.draftList[this.selectedList[idx]];
            this.submitEvent(eventObj);
            this.draftList.splice(this.selectedList[idx], 1);
            this.selectedList.splice(idx, 1);
        }
        localStorage.setItem('eventDraft', JSON.stringify(this.draftList));
      },
      submitEvent(eventObj){
        $.ajax({
          type : "post",
          url : this.$api+'/mc/event/report',
          data: JSON.stringify(eventObj),
          dataType : "json",
          success : function(data) {
            if(data.success == true){
              this.$Message.success('');
            }
          }.bind(this)
        });




        /*this.$http({
            method: 'post',
            url: this.$api+'/mc/event/report',
            data: Qs.stringify(eventObj),
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
            }
          }
        ) .then(function (response) {
          console.log(response);
          if(response.data.success == true){
            this.$Message.success('');
          }
        }.bind(this)).catch(function(error){
          console.log(error);
        })*/
      },
      deleteDraft(){
        for(var idx in this.selectedList){
           this.draftList.splice(this.selectedList[idx], 1);
           this.selectedList.splice(idx, 1)
        }
        localStorage.setItem('eventDraft', JSON.stringify(this.draftList));
      }
    }
  }
</script>



