<style scoped>
  .layout-content {
    background-color: rgb(247, 247, 247);
    height: 100%;
  }

  .layout-content-main {
    /* padding: 10px;*/
    font-size: 14px;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
  }

  .layout-ceiling-main a {
    color: #9ba7b5;
  }

  .item {
    border-bottom: 1px solid #8EBEC4;
    font-size: 14px;
    line-height: 35px;
  }

  .item > div {
    margin: auto 10px;
  }

  .submenu {
    background-color: #fff;
    border-bottom: 1px solid #8EBEC4;
  }

  .submenu > div {
    margin: auto 10px;
  }

  .layout-ceiling-main a {
    color: #9ba7b5;
  }

  .filter div {

    margin-left: 10px;
  }

  .item {
    background-color: #fff;
    margin-top: 10px;
    border-top: 1px solid #8EBEC4;
    border-bottom: 1px solid #8EBEC4;
  }

  .item-detail > div {
    text-align: left;
    margin: 5px auto;
  }

  i.icon.icon-form-name {
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/tx.svg);
  }

  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }

  .title {
    font-size: 15px !important;
    line-height: 35px;
  }

  .demo-carousel {
    text-align: center;
  }

  .approval {
    color: #ffffff;
    background-color: #2b85e4;
    text-align: center;
  }

  .approval-inuput {
    padding-left: 5px;
    border-radius: 5px;
    height: auto;
    width: 100%;
    max-width: 100%;
    font-size: 16px;
  }

  .submit-wrapper {
    display: flex;
    margin-top: 5px;
    text-align: center;
    justify-content: center;
  }

  .submit-approval {
    color: #ffffff;
    outline: 0;
    background-color: #2db7f5;
    width: 200px;
    height: auto;
    border-radius: 3px;
  }
  .approvalDescribe{
    display: flex;
    align-items: center;
  }

  .dot{
    background-color: #000000;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    margin-right: 10px;
  }

  .line {
    height: 2px;
    width: 100%;
    background-color: #2b85e4;
  }
</style>
<template>
  <div class="layout-content" v-if="inspection">
    <mt-header title="" class="topNav">
      <router-link :to="{name: 'main'}" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="layout-content-main">
      <div class="item" style="margin-top: 0px">
        <div class="submenu">
          <Row type="flex" justify="start" class="title">
            <Col span="19" style="text-align:left;">
            <span style="font-weight: 900"><strong>{{inspection.title}}</strong></span>
            </Col>
            <Col span="5" style="text-align:right;" v-if="inspection.status==3">
            {{getOvertimeDay(inspection.inspecttime)}}
            </Col>
          </Row>
        </div>
        <div class="item-detail">
          <Row>
            <Col span="24">
            <i class="icon icon-form-name"></i>&nbsp;&nbsp;{{inspection.inspectorName}}</Col>
          </Row>
          <div>
            &nbsp;
            <span>  &nbsp;&nbsp;{{inspection.floorno}}&nbsp;&nbsp;
                {{inspection.roomno}}&nbsp;&nbsp;{{inspection.roomName}}</span>
          </div>
          <div>
            &nbsp;{{inspection.orgname}}
          </div>
          <Row>
            <Col span="24">
             {{inspection.inspecttime|timeFilter}}</Col>
          </Row>
          <div v-if="inspection.limittime != null">
            {{inspection.limittime}}
          </div>
          <div>
            &nbsp;{{getTargetType(inspection.type)}}
          </div>
          <div>
            
            <span v-if="inspection.riskGrade==0"></span>
            <span v-else>{{inspection.riskGrade}}</span>
          </div>
          <div>
            &nbsp;{{inspection.desc}}
          </div>
          <div>
            
            <div style="">
              <Carousel arrow="always">
                <Carousel-item v-for="(item, index) in pictureList" :key="index">
                  <div class="demo-carousel">
                    <img :src="item" :style="{width: '250px',height: '150px'}"/>
                  </div>
                </Carousel-item>
              </Carousel>
            </div>
          </div>
        </div>
      </div>
      <div class="item">
        <div class="item-detail">
          <Row style="border-bottom: 1px solid #8EBEC4">
            <Col span="12">
            <i class="icon icon-form-name"></i>&nbsp;&nbsp;
            {{getHandler()}}</Col>
            <Col span="12" style="text-align:right;">
            {{getEventStatus(inspection.status)}}</Col>
          </Row>
          <Row>
            <Col span="24">
            {{getDeadline(inspection.inspecttime, inspection.limittime)}}</Col>
          </Row>
          <Row v-if="inspection.status == 4">
            <Col span="12">
            {{inspection.handletime|timeFilter}}</Col>
            <Col span="12" style="text-align:right;">
            {{inspection.grade}}</Col>
          </Row>
          <div v-if="inspection.status == 4" class="item-detail">
            <div> {{inspection.presult}}</div>
            <div>
              
              <div style="">
                <Carousel arrow="always">
                  <Carousel-item v-for="(item, index) in resultPicList" :key="index">
                    <div class="demo-carousel">
                      <img :src="item" :style="{width: '250px',height: '150px'}"/>
                    </div>
                  </Carousel-item>
                </Carousel>
              </div>
            </div>
          </div>

          <div>
            <Row>
              <Col span="4">
              <div class="approval"></div>
              </Col>
            </Row>
            <div class="line"></div>
            <ul>
              <li v-for="item in approvalList">
                <div class="approvalDescribe">
                  <div class="dot"></div>
                  <div style="font-weight: bold;line-height: 1.5;font-size: 16px">
                    {{item.describe }}</div>
                </div>

                <p style="margin-left: 25px;font-size: 16px">{{item.content}}</p>
              </li>

            </ul>
            <div v-if="getCookie('userToken').split('|')[2]==3|getCookie('userToken').split('|')[2]==4">
              <div style="margin-top: 20px">
                <textarea class="approval-inuput" v-model="approvals" placeholder=""/>
              </div>
              <div class="submit-wrapper">
                <div class="submit-approval" @click="submitText"><span>
                  
                </span></div>
              </div>
            </div>
          </div>
          <div style="text-align: center;margin-top: 20px;" v-if="inspection.status != 4 && getCookie('userToken').split('|')[2]==2">
            <Button type="info" style="width: 200px" @click="toRegister"></Button>
          </div>


        </div>
      </div>

      <div class="item">
        <div class="item-detail" @click="goFocusPage">
          
        </div>
      </div>
    </div>
  </div>
</template>
<script>

  import Xctp from '../assets/img/xctp.png'
  import Utils from '../assets/js/utils.js'
  import Vue from 'vue'
  import {Header} from 'mint-ui';
  import utils from '../assets/js/utils.js';

  Vue.component(Header.name, Header);
  export default {
    name: 'eventDetail',
    data() {
      return {
        xctp: Xctp,
        resultPicList: [],
        value1: 0,
        noticers: [],
        inspection: null,
        pictureList: [],
        loginStaff: "",
        approvals: "",
        token: "",
        approvalList: [],

        eventStatus: {
          1: '',
          2: '',
          3: '',
          4: ''
        },
        riskType: {
          3001: '',
          3002: '',
          3003: '',
          3004: '',
          3005: ''
        },
        targetType: {
          1001: '',
          1002: '',
          1003: '',
          1004: '',
          1000: ''
        },
        columns1: [
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'orgName'
          },
          {
            title: '',
            key: 'position'
          },
          {
            title: '',
            key: 'mobile'
          }
        ]
      }
    },
    created() {
      this.getDetail();
    },
    /*mounted(){
     this.$watch('inspection.status', function (newVal, oldVal) {
     alert('newVal:'+newVal+'oldVal:'+oldVal);
     this.getDetail();
     })
     },*/
    methods: {
      ...utils,
      goFocusPage() {
        this.$router.push({
          name: 'focus',
          params: {
            orgid: this.inspection.orgid,
            riskGrade: this.inspection.riskGrade
          }
        })
      },
      getOvertimeDay(inspecttime) {
        var overtimeDay = parseInt((new Date().getTime() - inspecttime) / 86400000 + '');
        return overtimeDay;
      },
      getDeadline(inspectTime, limitDay) {
        if (limitDay != null) {
          var time = inspectTime + limitDay * 86400000;
          return Utils.formatDate(time);
        } else {
          return '--'
        }
      },
      submitText() {
        var this_ = this;
        var userinfo = JSON.parse(utils.getCookie('userinfo'));
        $.ajax({
          type: "post",
          url: this.$api + "/inspectevent/addLeaderComment",
          data: {
            eventid: this.inspection.id,
            staffid: userinfo.id,
            comment: this.approvals,
            token: this.token,
          },
          dataType: "json",
          success: function (data) {
            var comments = data.obj.comments;
            this_.setComment(comments[comments.length-1])
            this_.approvals="";
          }
        })
      },
      setComment(comment){
        var d = new Date()
        var content = {};
        d.setTime(comment.createtime);
        var time = this.dateFormat(d);
        content.describe = time+" "+ comment.name + " " + comment.orgName + " " + comment.roleName;
        content.content=comment.comment;
        this.approvalList.push(content);
      },
      dateFormat(date, flag) {
        const year = date.getFullYear()
        const month = date.getMonth() + 1
        const day = date.getDate()
        const hour = date.getHours()
        const minute = date.getMinutes()
        const second = date.getSeconds()
        if (flag) {
          return [year, month, day].map(this.formatNumber).join('') + [hour, minute, second].map(this.formatNumber).join('')
        } else {
          return [year, month, day].map(this.formatNumber).join('-') + ' ' + [hour, minute, second].map(this.formatNumber).join(':')
        }
      },
      formatNumber (n) {
        n = n.toString()
        return n[1] ? n : '0' + n
      },
      getDetail() {
        var that = this;
        var cookie = utils.getCookie('token');
        if (cookie == null) {
          this.$router.push({path: '/login'})
        };
        this.token = cookie.split('|')[0];


        var cookie2 = utils.getCookie('userToken').split('|')[2]
        console.log(cookie2)
        $.ajax({
          type: "get",
          url: this.$api + '/inspectevent/detail',
          data: {
            inspectid: this.$route.params.id,
            roomId: this.$route.params.roomId,
            riskGrade: this.$route.params.riskGrade,
            target: this.$route.params.target,
            token: this.token,
          },
          dataType: "json",
          success: function (data) {
            if (data.success == true) {
              console.log(data.obj)
              this.noticers = data.obj.noticers;
              this.inspection = data.obj.inspection;
              this.loginStaff = data.obj.loginStaff;

              //
              if (this.inspection.ipicture != null
                && this.inspection.ipicture.length > 0) {
                var imgArr = this.inspection.ipicture.split(',');

                for (var i = 0; i < imgArr.length; i++) {
//                  var url = window.location.host + '/res/img' + imgArr[i];

                  var url = that.$apit + '/images' + imgArr[i];
                  this.pictureList.push(url);
                }
              } else {
                this.pictureList.push(this.xctp);
              }
              //
              if (this.inspection.status == 4 && this.inspection.ppicture != null
                && this.inspection.ppicture.length > 0) {
                var imgArr = this.inspection.ppicture.split(',');
                for (var i = 0; i < imgArr.length; i++) {
//                  var url = window.location.host + '/res/img' + imgArr[i];
                  var url = that.$apit + '/images' + imgArr[i];
                  this.resultPicList.push(url);
                }
              } else {
                this.resultPicList.push(this.xctp);
              }
              var comments = data.obj.comments;
              if (comments instanceof Array  && comments.length>=0){
                for (var i = 0; i < comments.length; i++) {
                  var comment = comments[i];
                  that.setComment(comment)
                }
              }


            } else {
              this.$Message.success('');
            }
          }.bind(this)
        });
      },
      toRegister() {
        this.$router.push({name: 'register', params: {id: this.inspection.id}});
      },
      getHandler() {
        if (this.inspection.handlerName != null && this.inspection.status==4) {
          return this.inspection.handlerName;
        }
        if (this.loginStaff != "" && this.token == 2) {//
          return this.loginStaff;
        }
        var handlers = '';
        return handlers;
      },
      getEventStatus(status) {
        return this.eventStatus[status];
      },
      getRiskType(type) {
        return this.riskType[type];
      },
      getTargetType(type) {
        return this.targetType[type];
      },
      goBack() {
        //eventBus.$emit('backToGarden');
        if (this.$route.query.from == 'buildingsDetail') {
          this.$router.push({name: 'buildingsDetail', params: {roomId: this.$route.params.roomId}});
        } else {
          this.$router.push({path: '/main/inspection'})
        }
      }
    }
  }

</script>



