Vue.filter('timeFilter', function(value) {
  var date = new Date(parseInt(value));
  var mon = parseInt(date.getMonth(), 10) + 1;
  var day = parseInt(date.getDate(), 10);
  var hour = parseInt(date.getHours(), 10);
  var min = parseInt(date.getMinutes(), 10);
  if (mon < 10)
    mon = "0" + mon;
  if (day < 10)
    day = "0" + day;
  if (hour < 10)
    hour = "0" + hour;
  if (min < 10)
    min = "0" + min;
  return date.getFullYear() + "-" + mon + "-" + day + " " + hour + ":" + min;
})



