<template>
  <div class="container">
    <mt-header title="" class="topNav">
      <router-link :to="{name: 'main'}" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <mt-button @click="confirm" slot="right"></mt-button>
    </mt-header>
    <div class="clear"></div>
    <div class="divide"></div>
    <div class="list">
      <ul v-for="name in names">
        <li>
          <div class="name">
            <i class="icon icon-form-name"></i>
            <label><span>{{name}}</span>
              <input class="check"  type="checkbox" :value="name" v-model="checkNames" align="right"></label>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default{
      name:"",
    data(){
      return {
        names:["","x",""],
        checkNames:[],
        saveName:[],
        orgid:"",
        grade:""
      }
    },
    mounted(){
      var that =this;
      this.orgid=this.$route.query.orgid;
      this.grade=this.$route.query.grade;
      if(this.$route.query.names && this.$route.query.names.length>0){
        this.saveName=this.$route.query.names;
        this.saveName.forEach(function (value) {
          that.checkNames.push(value)
        });
      }
      this.getPersonOfFocus()

    },
    methods: {
      getPersonOfFocus(){
        $.ajax({
          type: "get",
          url: this.$api + '/notice/find',
          data: {
            orgid: this.orgid,
            grade: this.riskGrade
          },
          dataType: "json",
          success: function (data) {
            if (data.success == true) {
              for (var i = 0; i < data.list.length; i++) {
                this.options[i] = {
                  label: data.list[i].name,
                  value: data.list[i].noticer
                }
              }
            }
          }.bind(this)
        });

      },
      confirm(){
          var that = this;
        this.saveName=[];//
        this.checkNames.forEach(function (value) {
          that.saveName.push(value);
        })

        this.$router.push({name:'focus',params:{orgid:this.orgid,riskGrade:this.grade},query:{checkNames:this.saveName}});
      }
    }
  }

</script>

<style scoped>
  .container {
    width: 100%;
    height: 100vh;
    background-color: #f4f4f4;
  }

  .head {
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: #ffffff;
  }

  .head > div:first-child {
    width: 100%;
    float: left;
    font-size: 20px;
    color: #a0a0a0;
  }

  .head > div:nth-child(2) {
    width: 20px;
    height: 30px;
    float: left;
    margin-left: -100%;
    background-color: red;
  }

  .head > div:last-child {
    color: #3f56ff;
    font-size: 16px;
    margin-left: -66px;
    float: left;
  }
.divide{
  height: 30px;
  font-size: 16px;
  color: #a0a0a0;
}
  .clear {
    clear: both;
  }

  ul > li {
    background-color: #ffffff;
    text-align: left;
    height: 43px;
    line-height: 43px;
    margin-bottom: 2px;
  }

  .list .name > span {
    display: inline-block;
    color: #a0a0a0;
    font-size: 18px;

  }

  i.icon.icon-form-name {
    margin-left: 15px;
    width: 29px;
    height: 29px;
    background-image: url(../assets/img/tx.svg);
  }
  i.icon {
    display: inline-block;
    vertical-align: middle;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    font-style: normal;
    position: relative;
  }

  .list .name > label {
    margin-left: 20px;
    color: #a0a0a0;
    font-size: 18px;
  }

  .list .name > label>span {
    display: inline-block;
    width: 14em;
  }


  .list .name .check{
    vertical-align: middle;
    width: 20px;
    height: 20px;
  }


</style>



